import React, { useContext } from 'react'
import { Formik , Form} from 'formik';
import { navigate } from 'gatsby'
import { injectIntl } from 'react-intl'
import { IoIosPlayCircle } from 'react-icons/io';

import Box from 'components/Box';
import Flex from 'components/Flex';
import Button from 'components/Button';
import Circle from 'components/Circle';
import Image from 'components/Image';
import Options from 'components/Options';
import FullpageVerticalCenter from 'components/FullpageVerticalCenter'

import AnswerContext from '../AnswerContext';

import request from 'utils/request';

import Layout from './Layout'

const Section = ({
  level,
  questions,
  order,
  subtitle,
  section,
  next,
  to,
  type,
  intl: { locale },
  ...props
}) => {
  const { setAnswer } = useContext(AnswerContext)
  return (
    <Layout height="100vh" {...props}>
      <Flex height="100%">
        <Box
          width={1 / 3}
          textAlign="center"
          color="white"
          px="4.75em"
          position="relative"
          bg="blue"
        >
          <Flex flexDirection="column" height="100%" justifyContent="space-around">
            <Box>
              <Box.inline
                px="5.25rem"
                py="1rem"
                color="blue"
                fontSize="1.5em"
                borderRadius="1.25em"
                bg="white"
              >
                {level}
              </Box.inline>
              <Box fontWeight="bold" pt="0.75rem" fontSize="5em"><Box.inline mx="0.25rem" verticalAlign="none"></Box.inline>{order}</Box>
              <Box fontWeight="bold" fontSize="3em">{subtitle}</Box>
            </Box>
            <Box
              ml="auto"
            >
              <Image src={section} />
            </Box>
          </Flex>
        </Box>
        <Box width={2 / 3} px="5.625em" position="relative">
          <Formik
            initialValues={{}}
            onSubmit={(values) => {
              setAnswer(values)
              navigate(`/${locale}/${to}`)
            }}
          >
            {({ handleChange, values }) => console.log(values) || (
              <Form>
                <FullpageVerticalCenter>
                  {questions.map(({ id, q }, index) => (
                    <Box key={id} mt={index && '2.625em'}>
                      <Flex alignItems="center">
                        <Box width="3.6875em" mr="0.875em">
                          <Circle fontSize="1.25em" bg="yellow">Q{id}</Circle>
                        </Box>
                        <Box flex={1} fontSize="1.25em">{q}</Box>
                      </Flex>
                      <Flex mt="1.5625em" alignItems="center">
                        <Box
                          px="2.125rem"
                          py="0.5625rem"
                          bg="blue"
                          color="white"
                          borderRadius="1.5em"
                          mr="1em"
                          fontSize="1.25em"
                        >
                           
                        </Box>
                        <Options
                          onChange={handleChange}
                          id={`a[${id - 1}]`}
                          flex={1}
                        />
                      </Flex>
                    </Box>
                  ))}
                  <Box position="absolute" right="0" bottom="0" transform="translateX(2.75em)">
                    <Button
                      type="submit"
                      px="1.5em"
                      borderRadius="1.5em"
                      rightIcon={IoIosPlayCircle}
                      disabled={!values.a || values.a.length < questions[2].id}
                    >{next}</Button>
                  </Box>
                </FullpageVerticalCenter>
              </Form>
            )}
          </Formik>
        </Box>
      </Flex>
    </Layout>
  )
}

export default injectIntl(Section)
